import React, { useState, useEffect } from 'react';
import "../layouts/chapter.css";
import { PYODIDE_URL, PYODIDE_INDEX_URL } from "../constants/pyodide";
import { PYTHON_PRINT_HELLO, PYTHON_PRINT_HELLO_LOOP, PYTHON_PRIME_NUM, PYTHON_READ_FILE } from "../contents/codeSnips";
import contents from "../contents/contentSnips";
import { Link } from 'react-router-dom';
import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-python";
import "ace-builds/src-noconflict/theme-github";
import "ace-builds/src-noconflict/ext-language_tools";

function Introduction() {
  const [pythonCodeHello, setPythonCodeHello] = useState(PYTHON_PRINT_HELLO);
  const [pythonCode1, setPythonCode1] = useState(PYTHON_PRINT_HELLO_LOOP);
  const [pythonCode2, setPythonCode2] = useState(PYTHON_PRIME_NUM);
  const [pythonCode3, setPythonCode3] = useState(PYTHON_READ_FILE);
  const [output, setOutput] = useState(null);
  const [output1, setOutput1] = useState(null);
  const [output2, setOutput2] = useState(null);
  const [output3, setOutput3] = useState(null);
  const maxLinesAce = 50;
  useEffect(() => {
    const script = document.createElement('script');
    script.src = PYODIDE_URL;
    script.async = true;
    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    }
  }, []);
  const runPydide = async (scriptText) => {
    const pyodide = await window.loadPyodide({
      indexURL: PYODIDE_INDEX_URL
    });
    const out = await pyodide.runPythonAsync(scriptText);
    return (out);
  };
  const runPythonHello = async () => {
    setOutput("Running...");
    const scriptText = pythonCodeHello;
    const out = await runPydide(scriptText);
    setOutput(out);
  };
  const runPython = async () => {
    setOutput1("Running...");
    const scriptText = pythonCode1;
    const out = await runPydide(scriptText);
    setOutput1(out);
  };
  const runPythonLoop = async () => {
    setOutput2("Running...");
    const scriptText = pythonCode2;
    const out = await runPydide(scriptText);
    setOutput2(out);
  };
  const runPythonFile = async () => {
    setOutput3("Running...");
    const scriptText = pythonCode3;
    const out = await runPydide(scriptText);
    setOutput3(out);
  };
  return (
    <div id="content" className="content">
      <main>
        <h1 id="loops-in-python"><a className="header" href="#loops-in-python">{contents.HEADING_1}</a></h1>
        <p>{contents.CONTENT_1}</p>
        <p>{contents.CONTENT_2}</p>
        <p>{contents.CONTENT_3}<code>{contents.CONTENT_3_1}</code>{contents.CONTENT_3_2}</p>
        <p>{contents.CONTENT_4}<code>{contents.CONTENT_4_1}</code>{contents.CONTENT_4_2}</p>
        <div className="divStyle">
          <button data-testid="python1" onClick={runPythonHello} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="pythonHello"
          theme="github"
          value={pythonCodeHello}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonCodeHello(value)}
          name="pythonHello"
          editorProps={{ $blockScrolling: true }}
        />
        <div>{output && <textarea
          name=""
          id="txt"
          data-testid="pythonResult1"
          className="resultTextareaStyle w-100"
          value={output}
        ></textarea>}</div>
        <p>{contents.CONTENT_5}</p>
        <div className="divStyle">
          <button data-testid="python2" onClick={runPython} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="python1"
          theme="github"
          value={pythonCode1}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonCode1(value)}
          name="python1"
          editorProps={{ $blockScrolling: true }}
        />
        <div>{output1 && <textarea
          name=""
          id="txt"
          data-testid="pythonResult2"
          className="resultTextareaStyle w-100"
          value={output1}
        ></textarea>}</div>
        <p>{contents.CONTENT_6}<code>{contents.CONTENT_6_1}</code>{contents.CONTENT_6_2}
          <code>{contents.CONTENT_6_3}</code>{contents.CONTENT_6_4}<code>{contents.CONTENT_6_5}</code>
          {contents.CONTENT_6_6}</p>
        <h1 id="prime-number"><a className="header" href="#prime-number">{contents.HEADING_2}</a></h1>
        <p>{contents.CONTENT_7}</p>
        <p>{contents.CONTENT_8}</p>
        <p>{contents.CONTENT_9}</p>
        <div className="divStyle">
          <button data-testid="python3" onClick={runPythonLoop} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="python2"
          theme="github"
          value={pythonCode2}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonCode2(value)}
          name="python2"
          editorProps={{ $blockScrolling: true }}
        />
        <div>{output2 && <textarea
          name=""
          id="txt"
          data-testid="pythonResult3"
          className="resultTextareaStyle w-100"
          value={output2}
        ></textarea>}</div>
        <h1 id="file-io"><a className="header" href="#file-io">{contents.HEADING_3}</a></h1>
        <p>{contents.CONTENT_10}</p>
        <div className="divStyle">
          <button data-testid="python4" onClick={runPythonFile} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="python3"
          theme="github"
          value={pythonCode3}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonCode3(value)}
          name="python3"
          editorProps={{ $blockScrolling: true }}
        />
        <div>{output3 && <textarea
          name=""
          id="txt"
          data-testid="pythonResult4"
          className="resultTextareaStyle w-100"
          value={output3}
        ></textarea>}</div>
      </main>
      <nav className="nav-wrapper" aria-label="Page navigation">
        <Link to="/chapter2" className="nav-chapters next">
          <i className="fa fa-angle-right"></i>
        </Link>
      </nav>
    </div>
  );
}

export default Introduction;
